import React from 'react';

import { Button } from '../../Button';
import type { DateRangePickerPeriod } from '../types';
import { Container } from './styles';

const periodLabelMap: Record<DateRangePickerPeriod, string> = {
    custom: '',
    today: 'Today',
    yesterday: 'Yesterday',
    last7Days: 'Last 7 days',
    last30Days: 'Last 30 days',
};

interface PeriodButtonsProps {
    periods: readonly DateRangePickerPeriod[];
    onChange: (value: DateRangePickerPeriod) => void;
}

export const PeriodButtons = ({ periods, onChange }: PeriodButtonsProps) => (
    <Container>
        {periods
            .filter((period) => period !== 'custom')
            .map((period) => (
                <Button key={period} appearance="primary" variant="outline" size="sm" onClick={() => onChange(period)}>
                    {periodLabelMap[period]}
                </Button>
            ))}
    </Container>
);
